// fetching endpoint using fetch api
async function fetchGridinfo(gridSize, commaSeperetedWords) {
    
    // ${} allows us to assign string in place
    let response = await fetch(`http://localhost:8080//wordGrid?gridSize=${gridSize}&wordList=${commaSeperetedWords}`);
    let result = await response.text();
    return result.split(" ");
}